
.header{
	display: grid;
	grid-template-columns: 60% 40%;
	grid-gap: 50px;
	align-items: center;
	font-size: 12px;
	color: rgba(255,255,255,255);
	overflow:hidden;

}

.leftheader{
	/*display: grid;
	grid-template-columns:50% 50%;*/
	/*grid-gap: 30px;*/
	display: flex;
	justify-content: center;
	align-items: center;
}
.lefttext{
	display: flex;
}
.lefttext p{
	margin-left: 40px;
	color: rgba(91,91,91,255);
	font-family: 'Open Sans', sans-serif;
	font-weight: 600;
}
.lefttext p:hover{
	cursor: pointer;
}

.rightheader{
	/*display: grid;
	grid-template-columns: 150% 50%;*/
	display: flex;
	justify-content: flex-start;
	background-color: var(--default-color);
	padding-left: 50px;
	transform: skewX(-24deg);
	height: 50px;
}
.rightheader > * { 
	transform: skewX(24deg); 
}
.righttext{
	display: flex;
	align-items: center;
	color: rgba(255,255,255,255);
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
	font-size: 12px;
}
.righttext a{
	text-decoration: underline;
	color: white;
}
.smallheader{
	/*display: flex;*/
	display: none;	
	align-items: center;
	justify-content: space-between;
	background-color: #f0ecec;
	position: relative;	
}
.smalltext{
	display: flex;
	/*flex-direction: column;*/
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	padding: 10px 10px 10px 10px;
	justify-content:flex-end;
	align-items: center;	
	width: 300px;
}
.smalltext p{
	margin: 0;
	font-size: 12px;
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
	color: rgba(91,91,91,255);
}
.smalltext a{
	color: rgba(91,91,91,255);
	text-decoration: underline;

}
.smalllogo{
	margin-left: 20px;
	transform: skewX(-34deg);
	background-color: var(--default-color);
	width: 150px;
	position: relative;
	right: 45px;
	height: 70px;
	display: flex;
	align-items: center;
}
.smalllogo > *{
	transform: skewX(34deg);

}
.smalllogo a{
	text-decoration: none;
	color: rgba(255,255,255,255);
	font-size: 18px;
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
	margin-left: 45px;
}

.nav{
	display: grid;
	grid-template-columns: 35% 65%;
	font-size: 12px;
	background-color: #f0ecec;
	overflow: hidden;
	justify-content: center;
	align-items: center;
}

.leftnav{
	display: flex;
	position: relative;
	width: 110%;
	right: 36px;
	height: 50px;
	align-items: center;
	justify-content: flex-end;
	background-color: var(--default-color); 
	padding: 10px;
	transform: skewX(-24deg);
}
.leftnav > * { 
	transform: skewX(24deg); 
}
.logo{
	position: relative;
	right: 200px;
}
.logo a{
	text-decoration: none;
	color: white;
	font-family: 'Bebas Neue', cursive;
	font-size: 25px;
	letter-spacing: 2px;
}
.rightnav{
	display: flex;
	align-items: center;
	padding: 10px;
	justify-content: space-around;
	height: 50px;
}
.nav ul{
	display: flex;	
	text-decoration: none;
	list-style-type: none;
	justify-content: center;
	margin: 0;
	align-items: center;
}
.nav ul li{
	margin-left: 30px;
	text-align: center;
}
.nav ul a{
	text-decoration: none;
	color: rgba(91,91,91,255);
	font-family: 'Open Sans', sans-serif;
	font-weight: 700;
}
.nav ul a:hover{
	color: grey;
}






